.navBar {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;

  border-bottom: 1px solid black;
}

.navlink {
  color: #ff0000;
  font-size: 30px;

  text-decoration: none;

  &.active {
    font-weight: 700;
  }
}
